<template>
  <erd-row class="m-0 p-0">
    <erd-spinner v-if="isLoading" size="lg" overlay absolute></erd-spinner>
    <v-shell
      class="terminal"
      :banner="banner"
      :shell_input="send_to_terminal"
      :commands="commands"
      @shell_output="prompt"
    ></v-shell>
  </erd-row>
</template>

<script>
/* eslint-disable no-unused-vars */
import { EventBus } from "../../../services/ebus";
import { httpService } from "../../../services/http";
import { mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {
      isLoading: false,
      send_to_terminal: "",
    };
  },
  computed: {
    ...mapGetters(["currentUser", "settings", "appXmltvSync"]),
    banner() {
      return {
        header: this.$t("terminal.header-1"),
        subHeader: this.$t("terminal.header-2"),
        helpHeader: this.$t("terminal.header-3"),
        emoji: {
          first: "",
          second: "",
          time: 1000,
        },
        sign: `[${this.currentUser.user.username}@iptv-tools] $`,
        img: {
          align: "left",
          link:
            "data:image/svg+xml;base64,",
          width: 50,
          height: 100,
        },
      };
    },
    commands() {
      return [
        {
          name: "user",
          get: this.cmdUser,
        },
        {
          name: "info",
          get() {
            return `<p>With ❤️ By Salah Bentayeb @halasproject.</p>`;
          },
        },
        {
          name: "uname",
          get() {
            return navigator.appVersion;
          },
        },
      ];
    },
  },
  methods: {
    ...mapActions(["syncXmltv"]),
    cmdUser() {
      let user = this.currentUser.user;
      return `Created........: ${this.formatDate(
        user.created
      )}<br>Last Updated...: ${this.formatDate(
        user.last_updated
      )}<br>Role...........: ${user.role} (${this.$t(
        `roles.${user.role}`
      )})<br>Status.........: ${user.status}<br>id.............: ${
        user.id
      }<br>username.......: ${user.username}`;
    },
    prompt(value) {
      console.log(this.currentUser);
      console.log(value);
    },
    formatDate(date) {
      return new Date(date).toLocaleString(
        this.$t("date.locale"),
        this.dateOptions
      );
    },
  },
  beforeMount() {},
  beforeDestroy() {},
};
</script>

<style lang="scss">
.terminal {
  .prompt {
    color: $theme-color-1 !important;
  }

  p {
    margin-top: 0.2rem;
    margin-bottom: 0.2rem;
  }

  h2 {
    margin-bottom: 0.35rem;
  }

  img {
    vertical-align: middle;
  }
}
